import React from 'react'
import useI18n from 'hooks/useI18n'
import styled from 'styled-components'
import { Text, Flex, Link, LinkExternal } from 'uikit'
import getLiquidityUrlPathParts from 'utils/getLiquidityUrlPathParts'

export interface ExpandableSectionProps {
  isBsc: boolean
  bscScanAddress?: string
  removed?: boolean
  totalValueFormated?: string
  lpLabel?: string
  quoteTokenAdresses?: string
  quoteTokenSymbol?: string
  type: number
  tokenAddresses: string
}

const Wrapper = styled.div`
  margin-top: 24px;
`

const StyledLinkExternal = styled(LinkExternal)`
  text-decoration: none;
  font-weight: normal;
  color: ${({ theme }) => theme.colors.text};
  display: flex;
  align-items: center;

  svg {
    padding-left: 4px;
    height: 18px;
    width: auto;
    fill: ${({ theme }) => theme.colors.primary};
  }
`

const getSwapUrl = (isBsc, swap: number) => {
  if (!isBsc) return 'https://swap.spiritswap.finance/#/'
  return swap === 0 ? 'https://exchange.pancakeswap.finance/#/' : 'https://dex.slime.finance/#/'
}

const DetailsSection: React.FC<ExpandableSectionProps> = ({
  isBsc,
  bscScanAddress,
  removed,
  totalValueFormated,
  lpLabel,
  quoteTokenAdresses,
  quoteTokenSymbol,
  tokenAddresses,
  type,
}) => {
  const TranslateString = useI18n()
  const liquidityUrlPathParts = getLiquidityUrlPathParts({ quoteTokenAdresses, quoteTokenSymbol, tokenAddresses })

  return (
    <Wrapper>
      <Flex justifyContent="space-between">
        <Text>{TranslateString(316, 'Stake')}:</Text>
        <StyledLinkExternal href={`${getSwapUrl(isBsc, type)}add/${liquidityUrlPathParts}`}>
          {lpLabel}
        </StyledLinkExternal>
      </Flex>
      {!removed && (
        <Flex justifyContent="space-between">
          <Text>{TranslateString(23, 'Total Liquidity')}:</Text>
          <Text>{totalValueFormated}</Text>
        </Flex>
      )}
      <Flex justifyContent="flex-start">
        <Link external href={bscScanAddress} bold={false}>
          {`View on ${isBsc ? 'BscScan' : 'FtmScan'}`}
        </Link>
      </Flex>
    </Wrapper>
  )
}

export default DetailsSection
