import React, { useEffect, useCallback, useMemo } from 'react'
import { Route, useRouteMatch } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { provider } from 'web3-core'
import { Heading } from 'uikit'
import { BLOCKS_PER_YEAR_BSC, BLOCKS_PER_YEAR_FTM } from 'config'
import FlexLayout from 'components/layout/Flex'
import Page from 'components/layout/Page'
import { useFarms, usePriceBnbBusd, usePriceABnb, calculateTotal, usePriceCakeBusd } from 'state/hooks'
import useRefresh from 'hooks/useRefresh'
import FarmCard, { FarmWithStakedValue } from './components/FarmCard/FarmCard'
import FarmTabButtons from './components/FarmTabButtons'
import { fetchFarmUserDataAsync } from '../../state/farms'

const Farms: React.FC = () => {
  const { path } = useRouteMatch()
  const farmsLP = useFarms()
  const aPrice = usePriceABnb()
  const cakePrice = usePriceCakeBusd()
  const bnbPrice = usePriceBnbBusd()
  const { chainId, account, ethereum }: { chainId: number; account: string; ethereum: provider } = useWallet()
  const dispatch = useDispatch()
  const { fastRefresh } = useRefresh()
  useEffect(() => {
    if (chainId && account) {
      dispatch(fetchFarmUserDataAsync(chainId, account))
    }
  }, [account, chainId, dispatch, fastRefresh])

  const isBsc = useMemo(() => parseInt(chainId as any) === 56, [chainId])

  const activeFarms = farmsLP.filter((farm) => farm.type === (isBsc ? 0 : 1) && !farm.ignore)
  const inactiveFarms = farmsLP.filter((farm) => farm.type === 1 && !farm.ignore)

  const farmsList = useCallback(
    (farmsToDisplay) => {
      const farmsToDisplayWithAPY: FarmWithStakedValue[] = farmsToDisplay.map((farm) => {
        const cakeRewardPerBlock = new BigNumber(farm.tokenPerBLock || 1)
          .times(new BigNumber(farm.poolWeight))
          .div(new BigNumber(10).pow(18))

        const cakeRewardPerYear = cakeRewardPerBlock.times(isBsc ? BLOCKS_PER_YEAR_BSC : BLOCKS_PER_YEAR_FTM)

        let apy = aPrice.times(cakeRewardPerYear)
        const totalValue = calculateTotal(farm, bnbPrice, aPrice, cakePrice)

        if (totalValue.comparedTo(0) > 0) {
          apy = apy.div(totalValue)
        }

        return { ...farm, apy, totalValue }
      })

      return farmsToDisplayWithAPY.map((farm) => (
        <FarmCard key={farm.pid} farm={farm} aPrice={aPrice} ethereum={ethereum} account={account} isBsc={isBsc} />
      ))
    },
    [bnbPrice, account, aPrice, ethereum, cakePrice, isBsc],
  )

  return (
    <Page>
      <FlexLayout>
        <div>
          <Heading as="h1" size="l" mb="30px" style={{ textAlign: 'center' }}>
            Gather Info
          </Heading>
        </div>
        {isBsc && (
          <div>
            <FarmTabButtons />
          </div>
        )}
      </FlexLayout>
      <div>
        <FlexLayout>
          <Route exact path={`${path}`}>
            {farmsList(activeFarms)}
          </Route>
          {isBsc && (
            <Route exact path={`${path}/a010`}>
              {farmsList(inactiveFarms)}
            </Route>
          )}
        </FlexLayout>
      </div>
    </Page>
  )
}

export default Farms
