import { useEffect, useState } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { useMasterchef } from './useContract'
import useRefresh from './useRefresh'

const useIsMember = () => {
  const [val, setVal] = useState(false)
  const { account, chainId } = useWallet()
  const { fastRefresh } = useRefresh()
  const masterChefContract = useMasterchef(chainId, 0)
  useEffect(() => {
    if (parseInt(chainId as any) !== 56) {
      setVal(true)
      return
    }
    if (account && !val) masterChefContract.methods.isMember().call({ from: account }).then(setVal)
  }, [account, chainId, val, masterChefContract, fastRefresh])
  return [val]
}

export default useIsMember
